import React, { useState } from "react";
import NavBar from "../components/NavBar";
import UploadSamplesComponent from "../components/uploadSample";
import ShowSamples from "../components/ShowSamples";
import ShowComparison from "../components/ShowComparison";
export default function Home() {
  const [newData, setNewData] = useState(0);
  const [newDataComparison, setNewDataComparison] = useState(0);
  return (
    <div>
      <NavBar homepage={"true"} />
      <UploadSamplesComponent newData={newData} setNewData={setNewData} />
      <ShowSamples newData={newData} />
      <ShowComparison
        newDataComparison={newDataComparison}
        setNewDataComparison={setNewDataComparison}
      />
    </div>
  );
}
