import React from "react";
import { Box ,Heading,Flex} from "@chakra-ui/react";
import BannerImage from "../assets/spacejoy-9M66C_w_ToM-unsplash.jpg";
export default function Banner() {
  return (
    <Box
      width={"100%"}
      height={"500px"}
      backgroundImage={`url(${BannerImage})`}
      backgroundPosition={"bottom"}
      backgroundSize={"cover"}
      position="relative"
    >
      <Flex
        width={"80%"}
        height={"70%"}
        backgroundColor={"rgba(60, 65, 64, 0.5)"}
        position={"absolute"}
        top={"50%"}
        left={"50%"}
        transform={"translate(-50%, -50%)"}
        border={"10px solid rgba(0, 0, 0, 0.5)"}
        borderRadius={"10px"}
        flexDir={"column"}
        alignItems={"center"}
        justifyContent={"center"}
      >
        <h1 className="banner" style={{ letterSpacing: "3px" }}>
          Let’s make it!
        </h1>
        <p className="subhedding">We will take care of your customers</p>
      </Flex>
    </Box>
  );
}
