import { Box, Flex, Heading, Text } from "@chakra-ui/react";

const PromiseComponent = () => {
  return (
    <Flex
      direction="column"
      alignItems="center"
      justifyContent="center"
      bg="#373B3B"
      color="white"
      p="40px"
    >
      <Heading
        as="h2"
        size="lg"
        mb="20px"
        className="subhedding"
        letterSpacing={"2px"}
      >
        We will Promise you
      </Heading>
      <Text textAlign="center" width={"500px"} letterSpacing={"1px"} lineHeight={"2"}>
        Interior design is a delicate blend of art and science, aimed at
        crafting interior spaces that are not just functional and comfortable,
        but also aesthetically pleasing. It's a holistic approach that
        encompasses various elements such as choosing finishes, positioning
        furniture, devising lighting plans, and selecting accessories.
      </Text>
    </Flex>
  );
};

export default PromiseComponent;
