import React, { useEffect, useState } from "react";
import { useAuthState } from "../context/AuthContext";
import {
  Box,
  Flex,
  Heading,
  Text,
  SimpleGrid,
  Image,
  Button,
  Tag,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
} from "@chakra-ui/react";
import { Slide } from "react-slideshow-image";
import "react-slideshow-image/dist/styles.css";
import BASE_URL from "../utils";

export default function ShowSamples({ newData }) {
  const { user } = useAuthState();
  const [selectedSample, setSelectedSample] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [samples, setSamples] = useState([]);
  const [expandedDescription, setExpandedDescription] = useState({});

  useEffect(() => {
    const fetchSamples = async () => {
      try {
        if (user && user._id) {
          const response = await fetch(
            `${BASE_URL}/work/uploadSample/${user._id}`
          );
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          const data = await response.json();
          console.log(data);

          const formattedSamples = data.designSample.map((sample) => {
            return {
              ...sample,
              // Map through the images array to extract src from each image object
              images: sample.images.map((image) => image.src),
            };
          });

          setSamples(formattedSamples || []);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchSamples();
  }, [user, newData]);

  const handleReadMore = (id, description) => {
    setExpandedDescription({
      ...expandedDescription,
      [id]: !expandedDescription[id],
    });
  };

  const openModal = (sample) => {
    setSelectedSample(sample);
    console.log(selectedSample);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  return (
    <Box p={4}>
      {samples.length > 0 ? (
        <SimpleGrid columns={[1, 1, 3]} spacing={4}>
          {samples.map((sample) => (
            <Flex
              key={sample._id}
              borderWidth="1px"
              borderRadius="lg"
              p={4}
              maxHeight={"400px"}
              overflow={"hidden"}
              gap={"10px"}
              boxShadow="0 4px 8px 0 rgba(0,0,0,0.2)"
              backgroundColor="#fff"
              flexDirection={["column", "column", "row"]}
              onClick={() => openModal(sample)}
            >
              <Image
                src={sample.images[0]}
                alt={sample.title}
                mb={2}
                maxHeight={"200px"}
                maxWidth={"50%"}
                borderRadius={"8px"}
              />

              <Box>
                <Heading
                  as="h2"
                  size="md"
                  mb={2}
                  color={"teal"}
                  fontWeight="bold"
                >
                  {sample.title}
                </Heading>
                <Text mb={2} color={"gray.600"}>
                  {expandedDescription[sample._id]
                    ? sample.description
                    : sample.description.slice(0, 100) + "..."}
                  <Button
                    variant="link"
                    color={"teal"}
                    onClick={() =>
                      handleReadMore(sample._id, sample.description)
                    }
                  >
                    {expandedDescription[sample._id]
                      ? "Read Less"
                      : "Read More"}
                  </Button>
                </Text>
                {/* <Flex flexWrap="wrap">
                  {sample.category &&
                    sample.category.map((cat, index) => (
                      <Tag key={index} mr={2} mb={2} colorScheme="teal">
                        {cat}
                      </Tag>
                    ))}
                </Flex> */}
              </Box>
            </Flex>
          ))}
        </SimpleGrid>
      ) : (
        <Heading as="h2" size="md">
          No Samples Uploaded
        </Heading>
      )}

      <Modal isOpen={isModalOpen} onClose={closeModal} size="xl">
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>{selectedSample && selectedSample.title}</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            {selectedSample && (
              <Box>
                <Slide>
                  {selectedSample.images.map((image, index) => (
                    <Image
                      key={index}
                      src={image}
                      alt={`Image ${index}`}
                      width="100%"
                      maxHeight="400px"
                      objectFit="contain"
                    />
                  ))}
                </Slide>

                <Text mb={2} color={"gray.600"}>
                  {expandedDescription[selectedSample._id]
                    ? selectedSample.description
                    : selectedSample.description.slice(0, 100) + "..."}
                  <Button
                    variant="link"
                    color={"teal"}
                    onClick={() =>
                      handleReadMore(
                        selectedSample._id,
                        selectedSample.description
                      )
                    }
                  >
                    {expandedDescription[selectedSample._id]
                      ? "Read Less"
                      : "Read More"}
                  </Button>
                </Text>
                {/* <Flex flexWrap="wrap">
                  {selectedSample.category &&
                    selectedSample.category.map((cat, index) => (
                      <Tag key={index} mr={2} mb={2} colorScheme="teal">
                        {cat}
                      </Tag>
                    ))}
                </Flex> */}
              </Box>
            )}
          </ModalBody>
          <ModalFooter></ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  );
}
