import { Box, Flex } from "@chakra-ui/react";

const Services = () => {
  return (
    <Flex
      bg="#C2C2BC"
      p="20px"
      justifyContent="center"
      alignItems="center"
      height="200px"
    >
      <Box
        width="100px"
        height="100px"
        bg="#373B3B"
        borderRadius="50%" // Set half of the width/height to achieve a circle
        p="20px"
        mx="10px"
      ></Box>
      <Box
        width="100px"
        height="100px"
        bg="#373B3B"
        borderRadius="50%" // Set half of the width/height to achieve a circle
        p="20px"
        mx="10px"
      ></Box>
      <Box
        width="100px"
        height="100px"
        bg="#373B3B"
        borderRadius="50%" // Set half of the width/height to achieve a circle
        p="20px"
        mx="10px"
      ></Box>
      <Box
        width="100px"
        height="100px"
        bg="#373B3B"
        borderRadius="50%" // Set half of the width/height to achieve a circle
        p="20px"
        mx="10px"
      ></Box>
    </Flex>
  );
};

export default Services;
