import React, { useState } from "react";
import {
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  FormControl,
  FormLabel,
  Input,
  Textarea,
  Select,
  Flex,
  Box,
} from "@chakra-ui/react";
import { useAuthState } from "../context/AuthContext";
import BASE_URL from "../utils";

const UploadSamplesComponent = ({ newData, setNewData }) => {
  const { user } = useAuthState();
  const [isOpenUploadModal, setIsOpenUploadModal] = useState(false);
  const [isOpenFormModal, setIsOpenFormModal] = useState(false);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [images, setImages] = useState([]);

  const designerID = user._id;

  const [category, setCategory] = useState({
    modern: false,
    contemporary: false,
    traditional: false,
  });
  const [interiorType, setInteriorType] = useState({
    kitchen: false,
    bedroom: false,
    office: false,
    bathroom: false,
    gallery: false,
  });

  const handleInteriorTypeChange = (e) => {
    const { name, checked } = e.target;
    setInteriorType((prevInteriorType) => ({
      ...prevInteriorType,
      [name]: checked,
    }));
  };
  const handleCategoryChange = (e) => {
    const { name, checked } = e.target;
    setCategory((prevCategories) => ({
      ...prevCategories,
      [name]: checked,
    }));
  };

  const [titleBA, setTitleBA] = useState("");
  const [descriptionBA, setDescriptionBA] = useState("");
  const [afterImage, setAfterImage] = useState(null);
  const [beforeImage, setBeforeImage] = useState(null);
  const [categoryBA, setCategoryBA] = useState("");

  const openUploadModal = () => {
    setIsOpenUploadModal(true);
  };

  const closeUploadModal = () => {
    setIsOpenUploadModal(false);
    // Reset fields when closing modal
    setTitle("");
    setDescription("");
    setImages([]);
    setCategory("");
  };

  const openFormModal = () => {
    setIsOpenFormModal(true);
  };

  const closeFormModal = () => {
    setIsOpenFormModal(false);
    // Reset fields when closing modal
    setTitle("");
    setDescription("");
    setImages([]);
    setCategory("");
  };
  const handleTitleChange = (e) => {
    setTitle(e.target.value);
  };

  const handleDescriptionChange = (e) => {
    setDescription(e.target.value);
  };

  const handleImagesChange = async (e) => {
    const selectedImages = e.target.files;
    console.log(selectedImages);

    try {
      const formData = new FormData();

      for (let i = 0; i < selectedImages.length; i++) {
        formData.append("images", selectedImages[i]);
      }

      const response = await fetch(
        `${BASE_URL}/work/uploadSample/sample/uploads`,
        {
          method: "POST",
          body: formData,
        }
      );

      const data = await response.json();
      console.log("Cloud URLs:", data.images);
      setImages(data.images);
    } catch (error) {
      console.error("Error uploading images:", error);
    }
  };

  const handleSubmit = async () => {
    try {
      const selectedCategories = Object.keys(category).filter(
        (cat) => category[cat]
      );

      // Convert interiorType object to an array of selected interior types
      const selectedTypes = Object.keys(interiorType).filter(
        (type) => interiorType[type]
      );

      const formattedImages = images.map((image, index) => ({
        index: index + 1,
        src: image.src,
      }));

      const formData = new FormData();
      formData.append("title", title);
      formData.append("description", description);
      formData.append("designerID", designerID);
      formData.append("images", JSON.stringify(formattedImages));

      // Append selected categories and types to the formData
      selectedCategories.forEach((selectedCategory) => {
        formData.append("category", selectedCategory);
      });

      selectedTypes.forEach((selectedType) => {
        formData.append("types", selectedType);
      });

      const response = await fetch(`${BASE_URL}/work/uploadSample`, {
        method: "POST",
        body: formData,
      });

      const data = await response.json();
      console.log("Data:", data.message);
      setNewData((current) => {
        return ++current;
      });
      closeUploadModal();
    } catch (error) {
      console.error("Error:", error);
      setNewData(false);
    }
  };

  const handleTitleChangeBA = (e) => {
    setTitleBA(e.target.value);
  };

  const handleDescriptionChangeBA = (e) => {
    setDescriptionBA(e.target.value);
  };

  const handleCategoryChangeBA = (e) => {
    setCategoryBA(e.target.value);
  };

  const handleAfterImageChange = async (e) => {
    const selectedImages = e.target.files[0];
    console.log(selectedImages);

    try {
      const formData = new FormData();
      formData.append("images", selectedImages);

      const response = await fetch(
        `${BASE_URL}/work/uploadSample/sample/uploads`,
        {
          method: "POST",
          body: formData,
        }
      );

      const data = await response.json();
      console.log("Cloud URLs:", data.images);
      setAfterImage(data.images[0].src);
    } catch (error) {
      console.error("Error uploading images:", error);
    }
  };

  const handleBeforeImageChange = async (e) => {
    const selectedImages = e.target.files;
    console.log(selectedImages);

    try {
      const formData = new FormData();

      for (let i = 0; i < selectedImages.length; i++) {
        formData.append("images", selectedImages[i]);
      }

      const response = await fetch(
        `${BASE_URL}/work/uploadSample/sample/uploads`,
        {
          method: "POST",
          body: formData,
        }
      );

      const data = await response.json();
      console.log("Cloud URLs Before:", data.images);
      setBeforeImage(data.images[0].src);
    } catch (error) {
      console.error("Error uploading image:", error);
    }
  };

  const handleSubmitBeforeAfter = async () => {
    console.log({
      title: titleBA,
      description: descriptionBA,
      afterImage,
      beforeImage,
      category: categoryBA,
    });

    try {
      const formData = new FormData();
      formData.append("title", titleBA);
      formData.append("description", descriptionBA);
      formData.append("category", categoryBA);
      formData.append("designerID", designerID);
      formData.append("AfterImages", afterImage);
      formData.append("BeforeImages", beforeImage);

      const response = await fetch(`${BASE_URL}/work/beforeAfter`, {
        method: "POST",
        body: formData,
      });

      const data = await response.json();
      console.log("Data:", data.BeforeAfter);
      closeUploadModal();
    } catch (error) {
      console.error("Error:", error);
    }
  };
  return (
    <Box width={"100%"}>
      <Flex
        alignItems={"center"}
        justifyContent={"center"}
        width="100%"
        padding={"20px"}
      >
        <Button onClick={openUploadModal}>Upload Samples</Button>
      </Flex>
      <Flex
        alignItems={"center"}
        justifyContent={"center"}
        width="100%"
        padding={"20px"}
      >
        <Button onClick={openFormModal}>Open Form</Button>
      </Flex>
      <Modal isOpen={isOpenUploadModal} onClose={closeUploadModal}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Upload Samples</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <FormControl>
              <FormLabel>Title</FormLabel>
              <Input type="text" value={title} onChange={handleTitleChange} />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>Description</FormLabel>
              <Textarea
                value={description}
                onChange={handleDescriptionChange}
              />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>Images</FormLabel>
              <Input type="file" multiple onChange={handleImagesChange} />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>Category</FormLabel>
              <div className="mb-4">
                <h3 className="font-semibold mx-2">Categories</h3>
                <div className="p-2">
                  <label className="block">
                    <input
                      type="checkbox"
                      name="modern"
                      checked={category.modern}
                      onChange={handleCategoryChange}
                    />
                    <span className="ml-3">Modern</span>
                  </label>
                  <label className="block">
                    <input
                      type="checkbox"
                      name="contemporary"
                      checked={category.contemporary}
                      onChange={handleCategoryChange}
                    />
                    <span className="ml-3">Contemporary</span>
                  </label>
                  <label className="block">
                    <input
                      type="checkbox"
                      name="traditional"
                      checked={category.traditional}
                      onChange={handleCategoryChange}
                    />
                    <span className="ml-3">Traditional</span>
                  </label>
                </div>
              </div>
            </FormControl>
            <FormControl>
              <FormLabel>Interior Type</FormLabel>
              <div className="mb-4">
                <h3 className="font-semibold mx-2">Type of Interior Design</h3>
                <div className="p-2">
                  <label className="block">
                    <input
                      type="checkbox"
                      name="kitchen"
                      checked={interiorType.kitchen}
                      onChange={handleInteriorTypeChange}
                    />
                    <span className="ml-3">Kitchen</span>
                  </label>
                  <label className="block">
                    <input
                      type="checkbox"
                      name="bedroom"
                      checked={interiorType.bedroom}
                      onChange={handleInteriorTypeChange}
                    />
                    <span className="ml-3">Bedroom</span>
                  </label>
                  <label className="block">
                    <input
                      type="checkbox"
                      name="office"
                      checked={interiorType.office}
                      onChange={handleInteriorTypeChange}
                    />

                    <span className="ml-3">Office</span>
                  </label>
                  <label className="block">
                    <input
                      type="checkbox"
                      name="bathroom"
                      checked={interiorType.bathroom}
                      onChange={handleInteriorTypeChange}
                    />

                    <span className="ml-3">Bathroom</span>
                  </label>
                  <label className="block">
                    <input
                      type="checkbox"
                      name="gallery"
                      checked={interiorType.gallery}
                      onChange={handleInteriorTypeChange}
                    />

                    <span className="ml-3">Gallery</span>
                  </label>
                </div>
              </div>
            </FormControl>
          </ModalBody>
          <ModalFooter>
            <Button onClick={handleSubmit}>Submit</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>

      <Modal isOpen={isOpenFormModal} onClose={closeFormModal}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Upload Form</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <FormControl>
              <FormLabel>Title</FormLabel>
              <Input
                type="text"
                value={titleBA}
                onChange={handleTitleChangeBA}
              />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>Description</FormLabel>
              <Textarea
                value={descriptionBA}
                onChange={handleDescriptionChangeBA}
              />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>Before Image</FormLabel>
              <Input type="file" onChange={handleBeforeImageChange} />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>After Image</FormLabel>
              <Input type="file" onChange={handleAfterImageChange} />
            </FormControl>
            <FormControl mt={4}>
              <FormLabel>Category</FormLabel>
              <Select value={categoryBA} onChange={handleCategoryChangeBA}>
                <option value="category1">Category 1</option>
                <option value="category2">Category 2</option>
                <option value="category3">Category 3</option>
              </Select>
            </FormControl>
          </ModalBody>
          <ModalFooter>
            <Button onClick={handleSubmitBeforeAfter}>Submit</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  );
};

export default UploadSamplesComponent;
