import React, { useEffect, useState } from "react";
import { useAuthState } from "../context/AuthContext";
import {
  Box,
  Flex,
  Heading,
  Text,
  SimpleGrid,
  Image,
  Button,
  Tag,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
} from "@chakra-ui/react";
import { Slide } from "react-slideshow-image";
import "react-slideshow-image/dist/styles.css";
import BASE_URL from "../utils";

export default function ShowComparison({ newData }) {
  const { user } = useAuthState();
  const [selectedSampleBeforeAfter, setSelectedSampleBeforeAfter] =
    useState(null);
  const [isBeforeAfterModalOpen, setIsBeforeAfterModalOpen] = useState(false);
  const [samplesBeforeAfter, setSamplesBeforeAfter] = useState([]);
  //   const [expandedDescription, setExpandedDescription] = useState({});

  useEffect(() => {
    const fetchSamples = async () => {
      try {
        if (user && user._id) {
          const response = await fetch(
            `${BASE_URL}/work/beforeAfter/${user._id}`
          );
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          const data = await response.json();
          console.log(data);

          const formattedSamples = data.beforeAfterSample.map((sample) => {
            return {
              ...sample,
            };
          });

          setSamplesBeforeAfter(formattedSamples || []);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchSamples();
  }, [user, newData]);

  const openModalBeforeAfter = (sample) => {
    setSelectedSampleBeforeAfter(sample);
    console.log(selectedSampleBeforeAfter);
    setIsBeforeAfterModalOpen(true);
  };

  const closeModalBeforeAfter = () => {
    setIsBeforeAfterModalOpen(false);
  };

  return (
    <Box p={4}>
      <h1>Comparison</h1>
      {samplesBeforeAfter.length > 0 ? (
        <SimpleGrid columns={[1, 1, 3]} spacing={4}>
          {samplesBeforeAfter.map((sample) => (
            <Flex
              key={sample._id}
              borderWidth="1px"
              borderRadius="lg"
              p={4}
              maxHeight={"400px"}
              overflow={"hidden"}
              gap={"10px"}
              boxShadow="0 4px 8px 0 rgba(0,0,0,0.2)"
              backgroundColor="#fff"
              flexDirection={["column", "column", "row"]}
              onClick={() => openModalBeforeAfter(sample)}
            >
              <Image
                src={sample.BeforeImages}
                alt={sample.title}
                mb={2}
                maxHeight={"200px"}
                maxWidth={"50%"}
                borderRadius={"8px"}
              />

              <Box>
                <Heading
                  as="h2"
                  size="md"
                  mb={2}
                  color={"teal"}
                  fontWeight="bold"
                >
                  {sample.title}
                </Heading>
                {/* <Text mb={2} color={"gray.600"}>
                  {expandedDescription[sample._id]
                    ? sample.description
                    : sample.description.slice(0, 100) + "..."}
                  <Button
                    variant="link"
                    color={"teal"}
                    onClick={() =>
                      handleReadMore(sample._id, sample.description)
                    }
                  >
                    {expandedDescription[sample._id]
                      ? "Read Less"
                      : "Read More"}
                  </Button>
                </Text> */}
                <Flex flexWrap="wrap">
                  {sample.category &&
                    sample.category.map((cat, index) => (
                      <Tag key={index} mr={2} mb={2} colorScheme="teal">
                        {cat}
                      </Tag>
                    ))}
                </Flex>
              </Box>
            </Flex>
          ))}
        </SimpleGrid>
      ) : (
        <Heading as="h2" size="md">
          No Samples Uploaded
        </Heading>
      )}

      <Modal
        isOpen={isBeforeAfterModalOpen}
        onClose={closeModalBeforeAfter}
        size="xl"
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>
            {selectedSampleBeforeAfter && selectedSampleBeforeAfter.title}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            {selectedSampleBeforeAfter && (
              <Box>
                <Slide>
                  <Image
                    src={selectedSampleBeforeAfter.BeforeImages}
                    alt={`Before Image`}
                    width="100%"
                    maxHeight="400px"
                    objectFit="contain"
                  />
                  <Image
                    src={selectedSampleBeforeAfter.AfterImages}
                    alt={`After Image`}
                    width="100%"
                    maxHeight="400px"
                    objectFit="contain"
                  />
                </Slide>

                
              </Box>
            )}
          </ModalBody>
          <ModalFooter></ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  );
}
