import { Flex, Heading, Box, Text } from "@chakra-ui/react";
import React from "react";
import WorkDesk from "../assets/interior-2685521_640.jpg";

export default function Promotion() {
  return (
    <Flex
      height={"300px"}
      overflow={"hidden"}
      backgroundColor={"#373B3B"}
      color={"white"}
      justifyContent={"space-evenly"}
      alignContent={"center"}
    >
      {/* Left Column */}
      <Box flex="1">
        <Flex
          p="10"
          paddingStart={"50px"}
          flexDir={"column"}
          height={"100%"}
          alignItems={"flex-start"}
          justifyContent={"center"}
          gap={"10px"}
        >
          <Heading className="titles" as="h1" size="lg">
            Hertical Texture
          </Heading>
          <Text
            mt="2"
            width={"90%"}
            className="subhedding"
            textTransform={"capitalize"}
            textStyle={"normal"}
          >
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla quis
            lorem ut libero malesuada feugiat. Lorem ipsum dolor sit amet,
            consectetur adipiscing elit. Nulla quis lorem ut libero malesuada
            feugiat. Lorem ipsum dolor sit amet, consectetur adipiscing elit.
            Nulla quis lorem ut libero malesuada feugiat.
          </Text>
        </Flex>
      </Box>

      {/* Right Column */}
      <Box flex="1">
        <Flex
          p="4"
          justifyContent={"center"}
          alignItems={"center"}
          width={"100%"}
          height={"100%"}
        >
          <img
            src={WorkDesk}
            alt="Work Desk"
            style={{ maxWidth: "70%", height: "70%" }}
          />
         
        </Flex>
      </Box>
    </Flex>
  );
}
