import React, { useState } from "react";
import { Flex, Button } from "@chakra-ui/react";
import { useNavigate } from "react-router-dom";
import SignupModal from "./SignupModal ";
import LoginModal from "./LoginModal";
import { useAuthState, useAuthDispatch } from "../context/AuthContext";

export default function NavBar({ homepage }) {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuthState();
  const dispatch = useAuthDispatch();
  const [isSignUpOpen, setIsSignUpOpen] = useState(false);
  const [isLoginOpen, setIsLoginOpen] = useState(false);

  const onCloseSignUp = () => setIsSignUpOpen(false);
  const onOpenSignUp = () => setIsSignUpOpen(true);

  const onCloseLogin = () => setIsLoginOpen(false);
  const onOpenLogin = () => setIsLoginOpen(true);

  const onLogOut = () => {
    dispatch({
      type: "LOGOUT",
    });
    localStorage.removeItem("token");
    navigate("/");
  };

  return (
    <Flex
      justifyContent={"space-between"}
      alignItems={"center"}
      width={"100%"}
      textColor={"white"}
      backgroundColor={"#373B3B"}
      height={"70px"}
      padding={"10px"}
    >
      <h1 className="titles">DPInterior</h1>
      {!homepage ? (
        <Flex
          justifyContent={"flex-end"}
          alignItems={"center"}
          width={"30%"}
          gap={"10px"}
        >
          <Button
            padding={"10px 13px"}
            borderRadius={"5px"}
            backgroundColor={"#373B3B"}
            color={"white"}
            onClick={onOpenSignUp}
          >
            SignUp
          </Button>
          <SignupModal isOpen={isSignUpOpen} onClose={onCloseSignUp} />
          <Button
            padding={"10px 13px"}
            borderRadius={"5px"}
            backgroundColor={"#373B3B"}
            color={"white"}
            onClick={onOpenLogin}
          >
            LogIn
          </Button>
          <LoginModal isOpen={isLoginOpen} onClose={onCloseLogin} />
        </Flex>
      ) : (
        <Flex
          justifyContent={"flex-end"}
          alignItems={"center"}
          width={"30%"}
          gap={"10px"}
        >
          <Button
            padding={"10px 13px"}
            borderRadius={"5px"}
            backgroundColor={"#373B3B"}
            color={"white"}
            onClick={onLogOut}
          >
            LogOut
          </Button>
        </Flex>
      )}
    </Flex>
  );
}
