import React, { useState, useEffect } from "react";
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  useMapEvents,
} from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

const customUserIcon = new L.Icon({
  iconUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png",
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
});

const Location = ({ clickedAddress, setClickedAddress }) => {
  const indiaCenter = [20.5937, 78.9629];
  const [clickedPosition, setClickedPosition] = useState(null);
  // const [clickedAddress, setClickedAddress] = useState("");

  useEffect(() => {
    if (clickedPosition) {
      fetchAddressFromCoordinates(clickedPosition);
    }
  }, [clickedPosition]);

  const handleMapClick = (e) => {
    setClickedPosition([e.latlng.lat, e.latlng.lng]);
  };

  const fetchAddressFromCoordinates = async (coordinates) => {
    const [lat, lon] = coordinates;
    setClickedPosition(coordinates);
    try {
      const response = await fetch(
        `https://nominatim.openstreetmap.org/reverse?lat=${lat}&lon=${lon}&format=json`
      );
      const data = await response.json();
      if (data && data.display_name) {
        setClickedAddress(data.display_name);
        console.log("Retrieved Address:", data.display_name); // Log the retrieved address
      }
    } catch (error) {
      console.error("Error fetching address:", error);
    }
  };

  return (
    <MapContainer
      center={indiaCenter}
      zoom={5}
      scrollWheelZoom={true}
      style={{ width: "400px", height: "400px" }}
    >
      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      />
      <ClickHandler handleClick={handleMapClick} />
      {clickedPosition && (
        <Marker position={clickedPosition} icon={customUserIcon}>
          <Popup>
            Clicked Location: {clickedPosition[0]}, {clickedPosition[1]}
            {clickedAddress && <p>Address: {clickedAddress}</p>}
          </Popup>
        </Marker>
      )}
    </MapContainer>
  );
};

const ClickHandler = ({ handleClick }) => {
  const map = useMapEvents({
    click: handleClick,
  });
  return null;
};

export default Location;
